
.feedNavBar {
  font-size: 18px;
  height: 45px;
  line-height: 45px;
  border-bottom: 1px #dedede solid;
  background-color: #fff;
  position: fixed;
  width: 100%;
  z-index: 3;
}
.feedNavBar:active {
    background-color: #fff;
}
.navLink {
  color: #999;
  height: 44px;
  display: block;
  text-align: center;
}



