
.detail {
  background-color: #fff;
  margin-bottom: 6px;
  padding: 10px 0;
}
.detail:active, .detail:focus {
    background-color: #fff;
}
.toolTop {
  padding-top: 5px;
  margin-top: 5px;
  border-top: 1px #e2e2e2 solid;
}
.usernameLine {
  margin-bottom: 6px;
}
.username {
  color: #333;
  font-size: 16px;
}
.content {
  font-size: 14px;
  color: #666;
  text-align: justify;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}
.timer {
  text-align: right;
  color: #ccc;
  font-size: 12px;
}



