
.comfirm {
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 6;
  bottom: 0px;
  left: 0px;
  position: fixed;
  overflow: auto;
  margin: 0;
  top: 0;
  right: 0;
}
.comfirm .comirmRow {
    position: absolute;
    width: 100%;
    bottom: 0;
}
.comfirm .comirmRow .comfirmCol {
      margin-top: 0;
}
.comfirm .comirmRow .comfirmCol:first-child {
        margin-bottom: 1vh;
}
.comfirm .comirmRow .comfirmCol button {
        background-color: #fff;
        font-size: 16px;
        color: #333;
        border-radius: 0;
        border: none;
}



