
.FeedImages {
  width: 100%;
}
.FeedImages img {
    width: 100%;
}
.imgParent {
  overflow: hidden;
}
.imgParent:before {
    content: "";
    display: block;
    padding-top: 100%;
}
.showImgBox {
  position: absolute;
  left: -3px;
  right: 0;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}
.showImgBox img {
    position: absolute;
    top: 0;
    left: 6px;
    bottom: 0;
    right: 0;
    -o-object-fit: cover;
       object-fit: cover;
    height: 100%;
    width: 100%;
}
.imagesRow {
  margin-bottom: 6px;
}



