
.content {
  padding: 10px 15px;
  background: #fff;
  border-radius: 10px;
  font-size: 14px;
}
.success {
  color: #59b6d7;
}
.success:before {
    font-size: 18px;
}
.error {
  color: red;
}
.error:before {
    font-size: 18px;
}
.notice {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: auto;
  z-index: 10;
  margin: 0;
  background: rgba(0, 0, 0, 0.3);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}



